import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BatchPrecreditComponent } from "./batch-pre-credit.component";
import { BatchPreCreditAddComponent } from "./batch-precredit-add/batch-precredit-add.component";
import { BatchPreCreditQuerykqComponent } from "./batch-pre-credit-querykq/batch-pre-credit-querykq.component";
import { BatchPreCreditQuerytjComponent } from "./batch-pre-credit-querytj/batch-pre-credit-querytj.component";

//客户群组
export const routes: Routes = [
  {
    path: '', component: BatchPrecreditComponent,


  },

  { path: 'batch-precredit-add', loadChildren: './batch-precredit-add/batch-precredit-add.module#BatchPrecreditAddModule' },
  {
    path: 'batch-precredit-querykq', component: BatchPreCreditQuerykqComponent
  },
  {
    path: 'batch-precredit-querytj', component: BatchPreCreditQuerytjComponent
  },


]

export const BatchPreCreditRouting: ModuleWithProviders = RouterModule.forChild(routes);
